<template>
  <div>
    <h1 style="color: pink; margin-left: 80px">注册</h1>
    <el-form :v-model="form" label-width="80px">
      <el-form-item label="用户名">
        <el-input v-model="form.account"></el-input>
      </el-form-item>
      <el-form-item label="密码">
        <el-input
          v-model="form.password"
          placeholder="密码"
          show-password
        ></el-input>
      </el-form-item>
      <el-form-item label="确认密">
        <el-input
          v-model="form.password_confirm"
          placeholder="确认密码"
          show-password
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button @click="register">注册</el-button>
        <el-button @click="login">登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { reqRegister } from "@/assets/api";
export default {
  name: "register",
  data() {
    return {
      form: {
        account: "",
        channel: "3",
        password: "",
        password_confirm: "",
      },
    };
  },
  methods: {
    register() {
      reqRegister(this.form).then(({ data }) => {
        if (data.code == 1) {
          this.$router.push("/login");
        } else {
          this.$message.error(data.msg);
        }
      });
    },
    login() {
      this.$router.push("/login");
    },
  },
};
</script>

<style lang="scss">
.el-input__inner {
  background: transparent !important;
  color: pink;
  border: 1px solid pink;
  width: 200px;
}
input::-webkit-input-placeholder {
  color: pink !important;
}
.el-form-item__label {
  color: pink;
}
.el-button {
  border: 1px solid pink;
  background: pink !important;
  color: #fff;
}
</style>
